.header{
  height: 100vh;
  background-image: linear-gradient(to right bottom, rgba(125, 133, 144, 0.8), rgba(127, 134, 145, 0.8)), url("../../assets/images/canal_1.jpg");
  background-size: cover;
  background-position: center;
  clip-path: polygon(0px 0px, 100% 0px, 100% 85%, 0px 100%);

  .banner{
    margin-bottom:100px;
    color: white;
    padding-top: 20vh;
    margin-left: 6vw;
    
    *{
      text-align: left;
      animation-name: slid;
      animation-duration: 2s;
    }

    h1 {
      font-size: 42px;
      line-height: 50px;
      margin-bottom: 30px;
    }
  
    h2 {
      font-size: 36px;
      line-height: 44px;
    }

    @media screen and (min-width:644px) and (max-width:991px){
      margin-left: 15vw;

      h1 {
        font-size: 56px;
        line-height: 64px;
      }

      h2 {
        font-size: 48px;
        line-height: 56px;
      }
    }

    @media screen and (min-width:992px) {
      margin-left: 15vw;

      h1 {
        font-size: 72px;
        line-height: 80px;
      }
    
      h2 {
        font-size: 56px;
        line-height: 64px;
      }
    }

    @keyframes slid {
      0% {
        opacity: 0;
        transform: translateY(-50px)
      }

      100% {
        opacity: 1;
        transform: translateY(0px)
      }
    }
  }

}