.experience{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .secquence{
    position: relative;
    width: 80vw;
    max-width: 1000px;

    .secquence_item{
      position: relative;
      padding: 15px;
      padding-left: 120px;
      // 動態載入效果
      opacity: 0;
      transform: translateY(-100px);
      transition: transform var(--animate_time) var(--animate_type), opacity var(--animate_time) var(--animate_type);

      span{
        color: white;
        background-color: var(--light-orange-color);
        padding: 5px 10px;
      }
      span::before{
        content: '';
        position: absolute;
        top: 10px;
        left: 70px;
        background-color: white;
        border: 8px solid var(--light-orange-color);
        border-radius: 50px;
        padding: 5px;
        z-index: 1;
      }

      h3{
        font-size: 1.5em;
        margin: 25px 0;
      }

      p{
        font-size: 1.2em;
        line-height: 1.5em;
        margin-bottom: 20px;
      }

      .tag_div{
        .tag{
          display: inline-block;
          color: var(--light-orange-color);
          border: 2px solid var(--light-orange-color);
          border-radius: 20px;
          margin: 10px 10px 0 0;
          padding: 10px 15px;
        }
      }

    }
    div::before{
      content:'';
      top: 15px;
      left: 82px;
      position: absolute;
      border-left: 3px solid gray;
      height: 100%;
    }

    .secq_animate{
      opacity: 1;
      transform: translateY(0px);
    }

    // 小螢幕樣式
    @media screen and (max-width:750px) {
      .secquence_item{
        padding-left: 50px;
        
        span::before {
          left: 0;
        }
      }
      div::before {
        left: 12px;
      }
    }
    

  }
}